import { AiOutlineEllipsis } from "react-icons/ai";
import { nanoid } from "nanoid";
import { useNavigate } from "react-router-dom";
import { useEffect, useRef, useState, lazy, Suspense } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AuthReducerProps, UserReducerProps } from "../../../interfaces";
import { RootState } from "../../../redux/store";
import {
  useCreateCollectionMutation,
  useDeleteCollectionMutation,
} from "../../../redux/rtkQuery/api";
import {
  getBrandsUserFollowing,
  getUserCollection,
  getUserDeals,
  getUserInfo,
} from "../../../redux/UserSlice";
import { Dispatch } from "redux";
import Images_Icons from "../../../constants/icons-images";
import Container from "../../../components/Container";
import Lottie from "lottie-react";
import empty from "../../../assets/json/empty.json";
import LoadingOverlay from "../../../components/LoadingOverlay";
import { GLOBAL_PREFIX } from "../../../constants";
import { GET_COLLECTIONS } from "../../../redux/CollectionSlice";
import { GET_ORDER } from "../../../redux/OrderSlice";

// const DashboardNavBar = lazy(
//   () => import("../../../components/DashboardNavBar")
// );

const NewDashboardNavBar = lazy(() => import("../../../components/NewDashboardNavBar"));

const MyDeals = lazy(() => import("../../../components/MyDeals"));

const MyBrands = lazy(() => import("../../../components/Mybrands"));

const DashboardSaved = (): JSX.Element => {
  let navigate = useNavigate();
  const dispatch = useDispatch<Dispatch>();
  const popRef = useRef<any>();
  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const { user_data }: UserReducerProps = useSelector((state: RootState) => state.userReducer);
  const { order_data, getOrder_loading } = useSelector((state: RootState) => state.orderReducer);
  const { user_collection, my_deals, my_deals_loading, my_followed_brands }: UserReducerProps =
    useSelector((state: RootState) => state.userReducer);
  const { collection_data, collection_loading } = useSelector(
    (state: RootState) => state.collectionReducer
  );

  useEffect(() => {
    if (token) {
      dispatch(GET_COLLECTIONS({ token }));
    }
  }, [token]);
  // console.log(collection_data, "this is payload");

  useEffect(() => {
    if (token) {
      dispatch(GET_ORDER({ token }));
      // dispatch(getBrandsUserFollowing({ token }));
    }

    // console.log(my_followed_brands);
  }, []);

  // console.log(order_data, "jjjsj");

  const { isOpen, onOpen, onClose } = useDisclosure();
  const {
    root,
    infoBox,
    font1,
    font2,
    editProfileText,
    contain,
    smallerDesc,
    singleCol,
    popContentStyle,
    colOptionText,
    cancelButton,
    nextButton,
    newCollectionInput,
    editProfileButton,
    iconCenter,
    editIconStack,
    imageDisplay,
    fontUnderline,
    nameStack,
  } = useStyles();
  const [createCollection] = useCreateCollectionMutation();
  const [deleteCollection] = useDeleteCollectionMutation();

  const [createCollectionLoading, setCreateCollectionLoading] = useState<boolean>(false);
  const [createCollectionError, setCreateCollectionError] = useState<string>("");
  const [collectionName, setCollectionName] = useState<string>("");
  const [loadingAction, setLoadingAction] = useState<boolean>(false);
  const [dealsFetching, setDealsFetching] = useState<boolean>(false);
  const [myDeals, setMyDeals] = useState<Array<any>>([]);
  const [activeId, setActiveId] = useState<any>(null);
  const [monthName, setMonthName] = useState<string>("");

  useEffect(() => {
    const monthNames = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];
    const today = new Date();
    const currentMonthName = monthNames[today.getMonth()];
    setMonthName(currentMonthName);
  }, []);

  const tabs = [
    { id: "1", name: "My deals" },
    { id: "2", name: "My saved" },
    { id: "3", name: "My brands" },
  ];

  // -----------------------------------------ADD COLLECTION FN------------------------------------------
  async function AddCollection(): Promise<void> {
    if (!collectionName) return;
    setCreateCollectionLoading(true);
    await createCollection({ name: collectionName })
      .then(({ data }: any) => {
        // console.log(data, "lllllll");
        if (data.status === 200) {
          dispatch(getUserCollection({ token }));
          onClose();
        }
      })
      .catch((e: any | unknown) => {
        setCreateCollectionError(e);
      })
      .finally(() => {
        setCreateCollectionLoading(false);
        setCollectionName("");
      });
  }

  async function DeleteCollection(id: string): Promise<void> {
    setLoadingAction(true);
    setActiveId(id);
    await deleteCollection(id)
      .then(({ data }: any) => {
        // console.log(data, "lllllll");
        if (data.status === 200) {
          dispatch(getUserCollection({ token }));
          onClose();
        }
      })
      .catch((e: any | unknown) => {
        setCreateCollectionError(e);
      })
      .finally(() => {
        setLoadingAction(false);
        setActiveId(null);
      });
  }

  function MySaved() {
    return (
      <>
        {/* -------------------------------------------------------COLLECTIONS SECTION----------------------------------------------- */}
        <Flex
          // bg={"green"}
          w={"100%"}
          justify={"space-between"}
          flexDir={"row"}
          flexWrap="wrap"
        >
          {/* <Flex {...singleCol} onClick={AddCollection}>
              <Flex h="100%" w="40%" flexDir={"row"} flexWrap={"wrap"}>
                {products4.slice(0, 4)?.map((e) => (
                  <Image
                    key={nanoid()}
                    h="50%"
                    w={"50%"}
                    bg={"blue"}
                    src={e.images}
                  />
                ))}
              </Flex>
              <Stack justify={"center"} ml="5%" w={"60%"}>
                <Text fontSize={"18px"}>All saved items</Text>
                <Text color={"#999999"}>20 items</Text>
              </Stack>
            </Flex> */}

          {collection_data?.collections?.map(
            // ({ id, name, likes }: { id: number; name: string; likes: any }, i: number) => (
            (e, i: number) => (
              <Flex
                key={nanoid()}
                {...singleCol}
                // onClick={() => navigate(`${name}`, { state: { id } })}
              >
                <Flex {...imageDisplay} flexDir={"row"} wrap={"wrap"}>
                  {e?.likes.map(({ offer }: { offer: any }) => (
                    <Image
                      key={nanoid()}
                      h="50%"
                      w={"50%"}
                      src={offer?.images[0]}
                      fallbackSrc={Images_Icons.appLogoPlaceholder}
                    />
                  ))}
                </Flex>
                <Stack justify={"center"} {...nameStack}>
                  <Text noOfLines={1} fontSize={"18px"}>
                    {e?.name}
                  </Text>
                  <Text color={"#999999"}>
                    {e.likes?.length} {e.likes?.length > 0 ? "items" : "item"}
                  </Text>
                </Stack>
                <Flex w="10%" h="15%" alignSelf={"center"}>
                  <Popover isLazy placement="bottom" initialFocusRef={popRef}>
                    {({ isOpen, onClose }) => (
                      <>
                        <PopoverTrigger>
                          <Box as="button">
                            {loadingAction && e.id.toString() === activeId ? (
                              <Spinner size={"sm"} />
                            ) : (
                              <Icon w={"100%"} h={"100%"} as={AiOutlineEllipsis} />
                            )}
                          </Box>
                        </PopoverTrigger>
                        {/* <Box as="button" textAlign={"left"}>
                          <PopoverContent ref={popRef} onClick={onClose} {...popContentStyle}>
                            <Stack {...editIconStack}>
                              {["Edit collection", "Delete"]?.map((e: any, i: number) => (
                                <Flex
                                  as={"button"}
                                  flexDir={"row"}
                                  align="center"
                                  key={nanoid()}
                                  onClick={() => {
                                    i === 0
                                      ? navigate(`${name}`, { state: { id } })
                                      : DeleteCollection(id.toString());
                                  }}
                                >
                                  <Image w={"8%"} h={"8%"} src={Images_Icons.edit_Icon} mr="5%" />
                                  <Text {...colOptionText}>{e}</Text>
                                </Flex>
                              ))}
                            </Stack>
                          </PopoverContent>
                        </Box> */}
                      </>
                    )}
                  </Popover>
                </Flex>
              </Flex>
            )
          )}

          {/* )} */}
          {/* -------------------------------------------------------ADD COLLECTIONS SECTION----------------------------------------------- */}
          <Flex {...singleCol} onClick={onOpen}>
            <Center {...iconCenter} flexDir={"row"} flexWrap={"wrap"}>
              <Icon w={"3vw"} h={"3vw"} as={IoIosAdd} />
            </Center>
            <HStack ml={"5%"}>
              <Text>New collection</Text>
            </HStack>
          </Flex>
        </Flex>
      </>
    );
  }

  // console.log(user_data);

  return (
    <Box w="100%" h="100vh" flexDir={"column"}>
      <Suspense fallback={<Spinner size="sm" />}>
        {/* <DashboardNavBar /> */}
        <NewDashboardNavBar />
      </Suspense>
      <Flex flexDir={"column"}>
        <Container>
          <Stack {...root}>
            {/* -------------------------------------------------------TOP SECTION----------------------------------------------- */}
            <HStack {...infoBox} bg="#F2F2F2">
              <Avatar
                size="xl"
                mr="2%"
                name={`${user_data?.customer?.name}`}
                src={user_data?.customer?.profilePicture}
              />
              <Stack>
                <Text {...font1} textTransform={"uppercase"}>
                  {user_data?.customer?.name}
                </Text>
                <Text {...font2}>
                  Member since {monthName} {new Date().getUTCFullYear()}
                </Text>
              </Stack>
              <Spacer />
              <Button
                variant="outline"
                {...editProfileButton}
                onClick={() => navigate(`${GLOBAL_PREFIX}/settings`)}
              >
                <Text {...editProfileText}>Edit Profile</Text>
              </Button>
            </HStack>

            {/* -------------------------------------------------------TABS SECTION----------------------------------------------- */}
            {/* <HStack {...contain}> */}
            <Tabs isLazy>
              <Flex flexDir={"row"} w={"100%"}>
                <TabList>
                  {tabs?.map((e) => (
                    <Tab
                      key={nanoid()}
                      {...smallerDesc}
                      _selected={{ color: "black", borderColor: "black" }}
                      // w={"80%"}
                      color="#999999"
                    >
                      <Text fontWeight="700">{e.name}</Text>
                    </Tab>
                  ))}
                </TabList>
                <Spacer />
                <HStack onClick={onOpen} cursor="pointer">
                  <Text fontWeight="600">New collection</Text>
                  <Icon w={"30px"} h={"30px"} as={IoIosAdd} />
                </HStack>
              </Flex>

              <TabPanels>
                <TabPanel px={0}>
                  <Suspense fallback={<Spinner size="sm" />}>
                    {order_data?.orders?.length > 0 ? (
                      <>
                        {order_data?.orders?.map((e) => (
                          <Skeleton isLoaded={!getOrder_loading} mb={"2px"} key={nanoid()}>
                            <MyDeals
                              name={e.offer.name}
                              price={Number(e.offer?.originalPrice)}
                              token={`${e?.offer?.tokens} ${e?.offer?.reward?.rewardSymbol}`}
                              quantity={e?.quantity}
                              date={e.offer.updatedAt}
                              expired={false}
                              image={e?.offer?.offerImages[0]?.url}
                              rest={{ ...e }}
                            />
                          </Skeleton>
                        ))}
                      </>
                    ) : (
                      <VStack>
                        <Flex w="500px" h="500px">
                          <Lottie animationData={empty} loop autoplay width={50} height={70} />
                        </Flex>

                        <Text>
                          You currently have no deals,{" "}
                          <Text
                            as="button"
                            onClick={() => navigate(`${GLOBAL_PREFIX}/dashboard/category`)}
                            {...fontUnderline}
                          >
                            Explore your favorite deals to purchase
                          </Text>
                        </Text>
                      </VStack>
                    )}
                  </Suspense>
                </TabPanel>
                <TabPanel px={0}>{MySaved()}</TabPanel>
                <TabPanel px={0}>
                  <Suspense fallback={<Spinner size="sm" />}>
                    {my_followed_brands?.length > 0 ? (
                      <>
                        {my_followed_brands?.map((e: any) => (
                          <Skeleton isLoaded={!my_deals_loading} mb={"2px"}>
                            <MyBrands key={nanoid()} item={{ ...e }} />
                          </Skeleton>
                        ))}
                      </>
                    ) : (
                      <VStack>
                        <Flex w="400px" h="400px">
                          <Lottie animationData={empty} loop autoplay width={50} height={70} />
                        </Flex>

                        <Text>
                          You currently have followed brands,{" "}
                          <Text
                            as="button"
                            onClick={() => navigate(`${GLOBAL_PREFIX}/dashboard/shop`)}
                          >
                            Explore your favorite brands
                          </Text>
                        </Text>
                      </VStack>
                    )}
                  </Suspense>
                </TabPanel>
              </TabPanels>
            </Tabs>
            {/* </HStack> */}
          </Stack>
        </Container>
      </Flex>
      {/* ----------------------------------------ADD COLLECTION MODAL-------------------------------------- */}
      <Modal
        isCentered
        blockScrollOnMount={true}
        scrollBehavior={"inside"}
        isOpen={isOpen}
        size={"xl"}
        onClose={onClose}
        motionPreset="slideInBottom"
        closeOnOverlayClick={false}
      >
        <ModalOverlay />
        <ModalContent w={{ base: "90vw", md: "30vw" }} h={"15vw"} borderRadius={0} p={"1%"}>
          <Text>Create new collection</Text>
          <Input
            variant="outline"
            {...newCollectionInput}
            placeholder="Collection name"
            value={collectionName}
            onChange={({ target }: { target: { value: string } }) => {
              setCollectionName(target.value);
            }}
          />
          <Button
            isLoading={createCollectionLoading}
            // as={Button}
            {...nextButton}
            onClick={AddCollection}
            disabled={!collectionName}
          >
            <Text>Next</Text>
          </Button>
          <Center {...cancelButton} onClick={onClose} as="button">
            <Text>Cancel</Text>
          </Center>
        </ModalContent>
      </Modal>
      {loadingAction && <LoadingOverlay />}
    </Box>
  );
};

export default DashboardSaved;

